  // ,   Safari
  function isSafari() {
    return /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
  }
  
  //    
  $(document).on("select2:open", function (e) {
    let select = $(e.target);
  
    if (isSafari()) {
      //  pointer-events     
      $(".select2-container--open").css("pointer-events", "none");
  
      //        
      setTimeout(function () {
        select.select2("close"); //  select2,    select
        select.trigger("focus"); //    select
      }, 0);
    }
  });
  
